import React, { Component } from 'react';
import { Grid, Cell, Card, Textfield, Button} from "react-mdl";

class Contact extends Component {
    render() {
        return (
            <div calssName="contact-page">
                <Grid style={{ padding: '40px 30px' }}>
                    <Card shadow={6} style={{ width: '100%',padding: '20px 30px'}}>
                        <div className="section-title">
                            <h2 className="page-title">
                                Contact <span>me</span>
                            </h2>
                            <div className="title-dvider">
                                <span></span> 
                                <span></span> 
                                <span></span> 
                            </div>
                        </div>
                        <Grid style={{ margin: '0'}}>
                            {/* Get Some Information Section */}
                            <Cell col={3} tablet={12} phone={12}>
                                <div className="contact-block-wrap">
                                    <div className="contact-block">
                                        <h5>Phone:</h5>
                                        <a href="tel:+14242284736">+1 424 228 4736</a>
                                        <a href="tel:+14242284738">+1 424 228 4738</a>
                                    </div>
                                    <div className="contact-block">
                                        <h5>email:</h5>
                                        <a href="mailto:example@gmail.com">example@gmail.com</a>
                                        <a href="mailto:example@gmail.com">example@gmail.com</a>
                                    </div>
                                    <div className="contact-block">
                                        <h5>SOCIAL:</h5>
                                        <a href="https://www.fb.com/">facebook</a>
                                        <a href="https://www.twitter.com/">twitter</a>
                                        <a href="https://www.plus.google.com/">google+</a>
                                    </div>
                                </div>

                            </Cell>
                            {/* Sen Message Section */}
                            <Cell col={9} tablet={12} phone={12}>
                                <form className="contact-form ">
                                    <div className="input-field">
                                        {/* Textfield with floating label */}
                                        <Textfield
                                            label="Your Name"
                                            floatingLabel
                                            className="input-wrap"
                                            inputClassName="input"
                                        />

                                        <Textfield
                                            label="Email Address"
                                            floatingLabel
                                            className="input-wrap"
                                            inputClassName="input"
                                        />

                                        {/* Numeric Textfield with floating label */}
                                        <Textfield
                                            pattern="-?[0-9]*(\.[0-9]+)?"
                                            error="Input is not a number!"
                                            label="Phone"
                                            floatingLabel
                                            className="input-wrap"
                                            inputClassName="input"
                                        />
                                    </div>

                                    <Textfield
                                        label="Your Message"
                                        floatingLabel
                                        className="input-wrap textarea"
                                        inputClassName="input"
                                        rows={12}
                                        
                                    />
                                    {/* Raised button with ripple */}
                                    <Button raised ripple>Contact Me</Button>
                                </form>
                            </Cell>
                        </Grid>
                    </Card> 
                </Grid> 
            </div>
        )
    }
}

export default Contact;